var vm = require('vm');

module.exports = function safeEval(code, context, opts) {
  var sandbox = {};
  var resultKey = 'SAFE_EVAL_' + Math.floor(Math.random() * 1000000);
  sandbox[resultKey] = {};
  var clearContext = "\n    (function(){\n      Function = undefined;\n      const keys = Object.getOwnPropertyNames(this).concat(['constructor']);\n      keys.forEach((key) => {\n        const item = this[key];\n        if(!item || typeof item.constructor !== 'function') return;\n        this[key].constructor = undefined;\n      });\n    })();\n  ";
  code = clearContext + resultKey + '=' + code;

  if (context) {
    Object.keys(context).forEach(function (key) {
      sandbox[key] = context[key];
    });
  }

  vm.runInNewContext(code, sandbox, opts);
  return sandbox[resultKey];
};